/* ? ======= popups start ======= */
.popups {
   position: fixed;
   width: 100%;
   height: 100vh;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   z-index: 999;
   // display: flex;
   justify-content: center;
   align-items: center;
   background-color: var(--color-azure);
   display: none;

   &__wrapper {
      width: 700px;
      min-width: 320px;
      color: var(--color-white);
      padding-left: 15px;
      padding-right: 15px;
   }

   &__title {
      margin: 0;
      font-size: clamp(1.125rem, 0.446rem + 3.39vw, 3.5rem);
      font-style: normal;
      font-weight: 600;
      line-height: 150%;
      letter-spacing: 3.9px;
   }

   &__form {
      margin-top: clamp(1.563rem, 1.116rem + 2.23vw, 3.125rem);
      display: flex;
      flex-direction: column;
   }

   &__label {
      display: flex;
      flex-direction: column-reverse;
   }

   &__label:not(:last-child) {
      width: 100%;
      height: 60px;
      border-bottom: 1px solid var(--color-white);

      &:focus &:active {
         border: none;
      }
   }

   &__input {
      background: transparent;
      width: 100%;
      height: 100%;
      outline: none;
      font-size: 20px;
      color: var(--color-white);

      &:focus &:active {
         border: none;
         background: transparent;
      }

      &::placeholder {
         color: var(--color-title);
         font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
      }
   }

   &__agremeent {
      margin-top: 22px;
      margin-bottom: 65px;
      font-size: clamp(0.75rem, 0.643rem + 0.54vw, 1.125rem);
      font-family: "Roboto", sans-sarif;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
   }

   &__button {
      width: 100%;
      height: clamp(4.375rem, 2.817rem + 7.79vw, 6.25rem);
      border-radius: var(--border-radius20);
      border: none;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: var(--color-white);

      font-size: clamp(1.25rem, 1.107rem + 0.71vw, 1.75rem);
      font-family: "Roboto", sans-serif;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      letter-spacing: 1.96px;
      color: var(--color-azure);
      transition: 0.8s;

      &:hover {
         transition: 0.8s;
         color: var(--color-vhite);
         background-color: var(--color-lochinvar);
      }
   }
}

.just-validate-error-label {
   color: black;
}

/* ? ======= popups media start ======= */
@media screen and (max-width: 700px) {
   .popups {
      &__wrapper {
         width: 100%;
      }
   }
}
