/* ? ======= footer start ======= */
.footer {
   width: 100%;
   background-color: var(--color-dianne);
   padding-top: clamp(2.063rem, 1.045rem + 5.09vw, 5.625rem);
   padding-bottom: 35px;

   &__container {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
   }

   &__logo {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;

      img {
         margin-bottom: 5px;
      }
   }

   &__logo-text {
      width: 160px;
      margin-left: 13px;

      font-family: var(--font-family);
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
      color: var(--color-white);
   }

   &__item {
      font-family: "Ubuntu";
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
      color: var(--color-white);
      transition: 0.5s;

      &:not(:first-child) {
         margin-top: 25px;
      }

      &:hover {
         color: var(--color-keppel);
         transition: 0.5s;
      }
   }

   &__contact {
      display: flex;
      align-items: center;
   }

   &__svg {
      width: 32px;
      height: 32px;

      margin-right: 15px;

      use {
         fill: var(--color-white);
      }
   }

   &__link-svg {
      height: 40px;
      font-family: "Ubuntu";
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
      color: var(--color-white);
      transition: 0.5s;

      display: flex;
      justify-content: flex-start;
      align-items: center;

      &:hover {
         color: var(--color-keppel);
         transition: 0.5s;
      }

      &:hover use {
         fill: var(--color-keppel);
         transition: 0.5s;
      }
   }

   &__copyright {
      font-family: "Open Sans", sans-serif;
      font-size: 16px;
      margin-top: 95px;
      display: block;
      text-align: center;
      color: #6e9ec1;
      margin-left: 15px;
      margin-right: 15px;
   }
}
/* ? ======= footer media start ======= */

@media screen and (max-width: 825px) {
   .footer {
      &__logo {
         display: none;
      }
   }
}

@media screen and (max-width: 600px) {
   .footer {
      &__container {
         flex-direction: column;
         align-items: flex-start;
         justify-content: space-between;
         width: 400px;
      }

      &__nav {
         margin-top: 50px;
      }

      &__item:not(:first-child) {
         margin-top: 15px;
      }

      &__contact:not(:first-child) {
         margin-top: 15px;
      }

      &__copyright {
         margin-top: 35px;
      }
   }
}

@media screen and (max-width: 400px) {
   .footer {
      &__container {
         width: 100%;
      }
   }
}
