/* ? ======= services start ======= */
.services {
   width: 100%;
   background-color: var(--color-dianne);
   color: var(--color-text);
   padding-top: clamp(2.5rem, 1.607rem + 4.46vw, 5.625rem);
   padding-bottom: clamp(2.5rem, 0.536rem + 9.82vw, 9.375rem);

   &__title {
      margin: 0;
      font-size: clamp(1.375rem, 0.929rem + 2.23vw, 2.938rem);
      font-weight: 700;
      letter-spacing: 7px;
      color: var(--color-title);
   }

   &__list {
      padding: 0;
      margin-top: clamp(1.25rem, 0.893rem + 1.79vw, 2.5rem);
      margin-bottom: 0px;
      list-style: none;
      font-size: clamp(1rem, 0.786rem + 1.07vw, 1.75rem);
      font-family: "Roboto", sans-serif;
      letter-spacing: 2px;
   }

   &__item:not(:first-child) {
      margin-top: 20px;
   }
}
/* ? ======= services media start ======= */
