/* ? ======= contact start ======= */
.contact {
   width: 100%;
   display: flex;
   justify-content: space-between;
   background-color: var(--color-azure);

   &__left {
      width: 50%;
      padding-top: clamp(1.875rem, 0.714rem + 5.8vw, 5.938rem);
      padding-bottom: clamp(1.875rem, 1.411rem + 2.32vw, 3.5rem);
      padding-left: clamp(0rem, -2.5rem + 12.5vw, 8.75rem);
      padding-right: clamp(0rem, -1.429rem + 7.14vw, 5rem);
      background-color: var(--color-azure);

      color: var(--color-white);
   }

   &__right {
      width: 50%;
      height: 100%;
   }

   &__label {
      display: flex;
      flex-direction: column-reverse;
   }

   &__map {
      width: 100%;
      height: 100%;
      background-color: var(--color-azure);
   }

   &__map-wrap {
      height: 100%;
   }

   &__iframe {
      width: 100%;
      height: 100vh;
      display: block;
      border: none;
   }

   &__title {
      margin: 0;
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
      font-style: normal;
      font-weight: 600;
      line-height: 150%;
      letter-spacing: 3.9px;
   }

   &__form {
      margin-top: clamp(1.563rem, 1.116rem + 2.23vw, 3.125rem);
      display: flex;
      flex-direction: column;
   }

   &__label:not(:last-child) {
      width: 100%;
      height: 60px;
      border-bottom: 1px solid var(--color-white);

      &:focus &:active {
         border: none;
      }
   }

   &__input {
      background: transparent;
      width: 100%;
      height: 100%;
      outline: none;
      font-size: 20px;
      color: var(--color-white);

      &:focus &:active {
         border: none;
         background: transparent;
      }

      &::placeholder {
         color: var(--color-title);
         font-size: 20px;
      }
   }

   &__agremeent {
      margin-top: 22px;
      margin-bottom: 65px;
      font-size: clamp(0.75rem, 0.643rem + 0.54vw, 1.125rem);
      font-family: "Roboto", sans-sarif;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
   }

   &__button {
      width: 100%;
      height: 100px;
      border-radius: var(--border-radius20);
      border: none;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: var(--color-white);
      cursor: pointer;

      font-size: 28px;
      font-family: "Roboto", sans-serif;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      letter-spacing: 1.96px;
      color: var(--color-azure);
      transition: 0.8s;

      &:hover {
         transition: 0.8s;
         color: var(--color-vhite);
         background-color: var(--color-lochinvar);
      }
   }
}

.just-validate-error-label {
   color: var(--color-meteorite) !important;
}

/* ? ======= contact media start ======= */
@media screen and (max-width: 1025px) {
   .contact {
      flex-direction: column;
      align-items: center;

      &__left,
      &__right {
         width: 100%;
      }

      &__left {
         width: 65%;
      }

      &__iframe {
         height: 50vh;
      }
   }
}

@media screen and (max-width: 705px) {
   .contact {
      &__left {
         width: 100%;
         padding-left: 15px;
         padding-right: 15px;
      }

      &__button {
         font-size: 20px;
         height: clamp(4.375rem, 2.817rem + 7.79vw, 6.25rem);
      }

      &__agremeent {
         margin-bottom: 30px;
      }
   }
}
