/* ? ======= benefits start ======= */
.benefits {
   background: linear-gradient(to right, var(--color-alto) 50%, var(--color-azure) 50%);
   &__container {
      width: 100%;
      display: flex;
      justify-content: space-between;
   }

   &__left {
      width: 50%;
      padding-top: 65px;
      padding-bottom: 65px;
      color: var(--color-dianne);
   }

   &__right {
      width: 50%;
      padding-top: 65px;
      padding-left: 80px;
      padding-bottom: 100px;
   }

   &__title {
      margin: 0;
      font-weight: 700;
      line-height: 100%;
      letter-spacing: 7px;
   }

   &__title--left {
      font-size: clamp(1.375rem, 0.929rem + 2.23vw, 2.938rem);
   }

   &__title--right {
      margin-bottom: 40px;
      font-size: clamp(1.125rem, 0.739rem + 1.93vw, 2.188rem);
      font-weight: 500;
      letter-spacing: 0px;
      line-height: 100%;
      color: var(--color-title);
   }

   &__list {
      padding: 0;
      margin-top: 40px;
      padding-left: 20px;
   }

   &__item {
      font-size: clamp(1rem, 0.893rem + 0.54vw, 1.375rem);
      font-family: "Roboto", sans-serif;
      font-weight: 400;
      line-height: 150%;
      padding-right: 15px;

      &:not(:last-child) {
         margin-bottom: 20px;
      }
   }

   /* ? ======= benefits slider start ======= */
   &__swiper {
      width: 80%;
      height: 80%;
      margin-left: 0;
   }

   &__slide {
      width: 100%;
      height: 90%;
      // margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
   }
   &__image {
      width: 120px;
      height: 120px;
      border-radius: 100%;
      overflow: hidden;
   }

   &__pic {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
   }

   &__slide-text {
      font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
      font-family: "Roboto", sans-serif;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      color: var(--color-text);
   }

   &__wrapper-navigation {
      height: 50px;
      width: 90%;
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   &__swiper-button::after {
      font-size: 35px;
      font-weight: 600;
      color: var(--color-keppel);
   }

   &__pagination {
      position: static;
      color: var(--color-keppel);
   }
}

.swiper {
   &-button-next &-button-prev {
      position: static;
      height: 10px;
   }

   &-pagination-bullet-active {
      background-color: var(--color-keppel);
   }
}

/* ? ======= benefits media start ======= */
@media screen and (max-width: 1025px) {
   .benefits {
      background: linear-gradient(to bottom, var(--color-azure) 50%, var(--color-alto) 50%);
      &__container {
         height: clamp(60.625rem, 59.774rem + 4.26vw, 62.5rem);
         flex-direction: column-reverse;
         align-items: center;
      }

      &__right {
         padding-left: 0;
         width: 80%;
      }

      &__left {
         width: 80%;
      }

      /* ? ======= benefits media swiper start ======= */

      &__swiper {
         width: 100%;
      }
   }
}

@media screen and (max-width: 671px) {
   .benefits {
      &__right {
         padding-left: 0;
         padding-bottom: 0;
         padding-top: 40px;
      }

      &__title--right {
         margin-bottom: 30px;
      }

      &__left {
         padding-bottom: 30px;
         padding-top: 0;
         padding-left: 10%;
      }
   }
}

@media screen and (max-width: 501px) {
   .benefits {
      &__wrapper-navigation {
         display: none;
      }

      &__right {
         padding-top: 30px;
         display: flex;
         flex-direction: column;
         justify-content: space-between;
      }

      &__left {
         width: 100%;
         padding-left: 0;
      }

      &__swiper {
         height: 100%;
      }

      &__slide {
         height: 100%;
         display: flex;
         flex-direction: column;
         justify-content: flex-start;
      }
      &__image {
         margin-bottom: 30px;
      }

      &__slide-text {
         margin: 0;
      }
   }
}
